.work-order-card {
  background: white;
  border: 1px solid #e2e8f0;
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  transition: all 0.2s ease;
  position: relative;
}
.work-order-card:hover {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
  border-color: #cbd5e1;
}
.work-order-card .bulk-selection-checkbox {
  position: absolute;
  top: 0.75rem;
  left: 0.75rem;
  z-index: 10;
}
.work-order-card .bulk-selection-checkbox .bulk-checkbox {
  -webkit-appearance: none;
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 9999px;
  border: 2px solid #60a5fa; /* blue-400 */
  background-color: #fff;
  box-shadow: 0 0 0 2px #fff, 0 0 0 4px rgba(59, 130, 246, 0.15); /* white outline + soft ring */
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: box-shadow 0.15s ease, border-color 0.15s ease;
}
.work-order-card .bulk-selection-checkbox .bulk-checkbox:checked {
  background-image: radial-gradient(#3b82f6 45%, transparent 46%); /* blue inner dot */
  background-repeat: no-repeat;
  background-position: center;
  background-size: 70% 70%;
  border-color: #3b82f6; /* blue-600 */
  box-shadow: 0 0 0 2px #fff, 0 0 0 6px rgba(59, 130, 246, 0.25);
}
.work-order-card .card-header .title-section {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex: 1;
}
.work-order-card .card-header .title-section .icon {
  color: #6b7280;
  font-size: 1rem;
}
.work-order-card .card-header .title-section .title {
  font-size: 1rem;
  font-weight: 600;
  color: #0f172a;
  margin: 0;
  line-height: 1.3;
}
.work-order-card .card-header .header-actions {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  flex-shrink: 0;
}
.work-order-card .card-header .badge {
  padding: 0.15rem 0.5rem;
  border-radius: 9999px;
  font-size: 0.7rem;
  font-weight: 500;
  text-transform: capitalize;
}
.work-order-card .card-header .badge.status-open {
  background: #fef2f2;
  color: #dc2626;
}
.work-order-card .card-header .badge.status-on-hold {
  background: #fef2f2;
  color: #dc2626;
}
.work-order-card .card-header .badge.status-draft {
  background: #f9f2fe;
  color: #5426dc;
}
.work-order-card .card-header .badge.status-in-progress {
  background: #eff6ff;
  color: #2563eb;
}
.work-order-card .card-header .badge.status-completed {
  background: #f0fdf4;
  color: #16a34a;
}
.work-order-card .card-header .badge.status-cancelled {
  background: #f3f4f6;
  color: #6b7280;
}
.work-order-card .card-header .badge.priority-low {
  background: #f0fdf4;
  color: #16a34a;
}
.work-order-card .card-header .badge.priority-medium {
  background: #eff6ff;
  color: #2563eb;
}
.work-order-card .card-header .badge.priority-high {
  background: #fef3c7;
  color: #d97706;
}
.work-order-card .card-header .badge.priority-ppm {
  background: #e2c7fe;
  color: #3b06d9;
}
.work-order-card .card-header .badge.priority-critical {
  background: #fef2f2;
  color: #dc2626;
}
.work-order-card .card-content {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.work-order-card .card-content .description {
  color: #64748b;
  font-size: 0.875rem;
  margin-bottom: 0.75rem;
  line-height: 1.5;
}
.work-order-card .card-content .meta-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.5rem 2rem;
  margin-top: 0.5rem;
}
.work-order-card .card-content .meta-row .meta-item {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  color: #334155;
  font-size: 0.9rem;
}
.work-order-card .card-content .meta-row .meta-item .icon {
  width: 16px;
  height: 16px;
  color: #94a3b8;
}
.work-order-card .card-content .footer-row {
  margin-top: 0.75rem;
  padding-top: 0.75rem;
  border-top: 1px solid #e5e7eb;
  color: #64748b;
  font-size: 0.85rem;
}
.work-order-card .card-footer {
  display: none;
}

.list-view.work-order-card {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  min-height: auto;
  padding: 1rem;
}
.list-view.work-order-card .card-header {
  margin-bottom: 0;
  flex: 1;
  min-width: 200px;
}
.list-view.work-order-card .card-content {
  flex: 2;
}
.list-view.work-order-card .card-content .details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 0.75rem;
}
.list-view.work-order-card .card-footer {
  display: none;
}

@media (max-width: 768px) {
  .work-order-card {
    padding: 1rem;
    min-height: 180px;
  }
  .work-order-card .card-header {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }
  .work-order-card .card-header .badges {
    align-self: flex-start;
  }
  .work-order-card .card-content .details {
    gap: 0.5rem;
  }
  .work-order-card .card-footer {
    display: none;
  }
  .list-view.work-order-card {
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
    min-height: auto;
  }
  .list-view.work-order-card .card-content .details {
    grid-template-columns: 1fr;
  }
  .list-view.work-order-card .card-footer {
    text-align: left;
    width: 100%;
    border-top: 1px solid #f3f4f6;
    padding-top: 1rem;
    margin-top: 1rem;
  }
  .list-view.work-order-card .card-footer .button-group {
    flex-direction: row;
    gap: 0.75rem;
  }
  .list-view.work-order-card .card-footer .button-group .view-button,
  .list-view.work-order-card .card-footer .button-group .edit-button {
    flex: 1;
    min-width: 0;
  }
}
